import styled, { css } from '@jupiter/plugin-runtime/styled';

export enum ButtonTheme {
  Light,
  Dark,
}

export interface ButtonProps {
  colorTheme: ButtonTheme;
  icon?: string;
  height?: number;
  width?: number;
  children: React.ReactText;
  onClick: React.MouseEventHandler;
  zIndex?: number;
  disabled?: boolean;
}

const StyledButton = styled.button<
  Pick<ButtonProps, 'height' | 'colorTheme' | 'width' | 'zIndex'>
>`
  display: flex;
  justify-content: center;
  align-items: center;
  height: ${props => `${props.height || 42}px`};
  width: ${props => `${props.width || 168}px`};
  color: #fff;
  background-color: #262627;
  font-size: 16px;
  line-height: 16px;
  outline: none;
  appearance: none;
  border-style: none;
  z-index: ${props => (props.zIndex ? props.zIndex : 0)};
  ${props => {
    return (
      props.colorTheme === ButtonTheme.Light &&
      css`
        background-color: #fff;
        border: 1px solid #333;
        color: #333;
      `
    );
  }}

  &:hover {
    cursor: pointer;
  }

  &:disabled {
    opacity: 0.2;
  }
`;

const StyledButtonIcon = styled.img`
  height: 16px;
  width: 16px;
  margin-right: 8px;
`;

const Button = ({
  colorTheme,
  icon,
  height,
  width,
  children,
  onClick,
  zIndex,
  disabled = false,
}: ButtonProps) => {
  return (
    <StyledButton
      colorTheme={colorTheme}
      height={height}
      width={width}
      onClick={onClick}
      zIndex={zIndex}
      disabled={disabled}>
      {icon ? <StyledButtonIcon src={icon} /> : null}
      {children}
    </StyledButton>
  );
};

export default Button;
