import whiteLogoIcon from '@/assets/logo-text-white.svg';
import blackLogoIcon from '@/assets/logo-text.svg';
import { useHistory } from '@jupiter/plugin-runtime/router';
import { LayoutTheme } from '../Layout';
import styles from './index.module.css';

interface LogoProps {
  theme: LayoutTheme;
  disableBootom?: boolean;
}

const Logo = ({ theme, disableBootom = false }: LogoProps) => {
  const history = useHistory();
  return (
    <div className={styles.logo} onClick={() => history.push('/')}>
      <img
        src={theme === LayoutTheme.Dark ? whiteLogoIcon : blackLogoIcon}
        className={styles.logoIcon}
        alt="logo"
      />
      {!disableBootom ? (
        <span
          className={styles.logoBottom}
          style={{
            borderColor: theme === LayoutTheme.Dark ? '#fff' : '#000',
          }}></span>
      ) : null}
    </div>
  );
};

export default Logo;
