import Button, { ButtonTheme } from '@/components/button';
import githubIcon from '@/assets/github.svg';
import bannerLogo from '@/assets/banner-logo.png';
import Layout, { LayoutTheme } from '@/components/Layout';
import { useHistory } from '@jupiter/plugin-runtime/router';
import textIcon from '@/assets/text-icon.svg';
import Logo from '@/components/Logo';
import React, { useState } from 'react';
import styles from './index.module.css';

const Home = () => {
  const history = useHistory();

  const linkRef = React.createRef<HTMLAnchorElement>();

  const [showBottomMask, setShowBottomMask] = useState(true);

  const toLogin = () => {
    history.push('/login');
  };

  const closeBottomMask = () => {
    setShowBottomMask(false);
  };

  return (
    <Layout
      showFooter={false}
      showBootomMask={showBottomMask}
      title="Modern.js"
      onClickBottom={ret => {
        if (ret) {
          toLogin();
        } else {
          closeBottomMask();
        }
      }}>
      <div className={styles.home}>
        <div className={styles.homeMain}>
          <div className={styles.homeMainHeader}>
            <Logo theme={LayoutTheme.Dark} disableBootom={true} />
          </div>
          <div className={styles.homeMainBody}>
            <div className={styles.info}>
              <div className={styles.infoLogo}>
                <Logo theme={LayoutTheme.Dark} disableBootom={true} />
              </div>
              <p className={styles.infoDesc}>
                Let's go gentle into that good era of modern web development
              </p>
              <div className={styles.buttonGroup}>
                <Button
                  colorTheme={ButtonTheme.Dark}
                  width={152}
                  icon={textIcon}
                  onClick={toLogin}>
                  内测邀请问卷
                </Button>

                <a
                  ref={linkRef}
                  href="https://github.com/modern-js-dev/modern.js"
                  target="_blank"
                  style={{ display: 'none' }}>
                  github
                </a>

                <Button
                  colorTheme={ButtonTheme.Light}
                  icon={githubIcon}
                  width={162}
                  onClick={() => {
                    /** empty */
                    linkRef.current?.click();
                  }}>
                  Github
                </Button>
              </div>
            </div>
            <div className={styles.bannerLogo}>
              <img src={bannerLogo} alt="banner logo" />
            </div>
          </div>
        </div>
        {/* <div className={styles['logo-shadow-wrapper']}>
          <img className={styles.logoShadow} src={logoShadowIcon} />
        </div> */}
        <div className={styles.extra}>
          {/* <img src={circleIcon} className={styles.extraIcon} /> */}
          <p>
            Powered by{' '}
            <a
              className={styles.extraTitle}
              href="https://webinfra.org/"
              target="_blank">
              ByteDance Web Infra
            </a>
          </p>
          {/* <img src={arrowRightIcon} className={styles.extraLinkIcon} /> */}
        </div>
        <div className={styles.bottomLine}></div>
        {/* <div className={styles.blackMask}></div> */}
      </div>
    </Layout>
  );
};

export default Home;
