import Footer from '@/components/Footer';
import React from 'react';
import whiteBgIcon from '@/assets/white-bg.svg';
import okIcon from '@/assets/ok.svg';
import closeIcon from '@/assets/close.svg';
import { Helmet } from '@jupiter/plugin-runtime/head';
import styles from './index.module.css';

export enum LayoutTheme {
  Dark,
  Light,
}

export interface LayoutProps {
  children: React.ReactNode;
  theme?: LayoutTheme;
  progress?: number | string;
  showBootomMask?: boolean;
  onClickBottom?: (x: boolean) => any;
  title?: string;
  backgroundStyle?: Record<string, unknown>;
  showFooter?: boolean;
}

const Layout = ({
  children,
  theme,
  progress,
  showBootomMask = false,
  onClickBottom,
  title = 'Modern.js | 问卷',
  backgroundStyle = {
    background: '#fff',
  },
  showFooter = true,
}: LayoutProps) => {
  return (
    <div className={styles.wrapper} style={backgroundStyle}>
      <Helmet>
        <title>{title}</title>
      </Helmet>
      {progress ? (
        <div className={styles.progressBar} style={{ width: progress }}></div>
      ) : null}
      <div className={styles.main}>
        <div className={styles.container}>{children}</div>
        {showFooter ? <Footer theme={theme} /> : null}
        {showBootomMask ? (
          <div className={styles.bottomMaskWrapper}>
            <div className={styles.bottomMask}>
              <div>
                <p>😊 &nbsp; 邀请你参加内测问卷调查</p>
                <img
                  src={okIcon}
                  className={styles.bottomMaskOk}
                  onClick={() => onClickBottom!(true)}
                  alt="ok"
                />
              </div>
              {/* <img
                src={closeIcon}
                className={styles.bottomMaskClose}
                onClick={() => onClickBottom!(false)}
              /> */}
            </div>
          </div>
        ) : null}
      </div>
    </div>
  );
};

export default Layout;
