import { Switch, Route } from '@jupiter/plugin-runtime/router';
import loadable from '@jupiter/plugin-runtime/loadable';
import './App.global.css';
import Home from '@/containers/Home';
import '@/utils/rem';

const Questions = loadable(
  () => import(/* webpackPreload: true */ '@/containers/Questions'),
);
const Success = loadable(() => import('@/containers/Success'));
const Login = loadable(
  () => import(/* webpackPreload: true */ '@/containers/Login'),
);
const Join = loadable(
  () => import(/* webpackPreload: true */ '@/containers/Join'),
);

const App: React.FC = () => {
  return (
    // <MouseContextProvider>
    <Switch>
      <Route exact={true} path="/" component={Home} />
      <Route exact={true} path="/login" component={Login} />
      <Route
        exact={true}
        path="/questions/:currentStep"
        component={Questions}
      />
      <Route exact={true} path="/success/:isSuccess" component={Success} />
      <Route exact={true} path="/join" component={Join} />
      <Route path="*">
        <div>404</div>
      </Route>
    </Switch>
    // </MouseContextProvider>
  );
};

export default App;
